<template>
    <div>
      <input v-model="newTodo" @keyup.enter="submitTodo" placeholder="Add a new to-do" />
      <button @click="submitTodo">Add</button>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        newTodo: ''
      }
    },
    methods: {
      submitTodo() {
        if (this.newTodo.trim() !== '') {
          this.$emit('addTodo', this.newTodo.trim())
          this.newTodo = ''
        }
      }
    }
  }
  </script>
  
  <style scoped>
  /* 스타일을 여기에 추가하세요 */
  </style>