<template>
    <li :class="{ completed: todo.completed }">
        <input type="checkbox" @change="$emit('toggle-completed')" />
      {{ todo.text }}
      <button @click="$emit('remove')">Remove</button>
    </li>
</template>
  
<script>
  export default {
    props: ['todo']
  }
</script>
  
<style scoped>
  /* 스타일을 여기에 추가하세요 */
  .completed {
  text-decoration: line-through;
  color: #999;
}
</style>