<template>
    <div>
      <h1>My To-Do List</h1>
      <ul>
        <TodoItem 
          v-for="(todo, index) in todos" 
          :key="index" 
          :todo="todo" 
          @remove="removeTodo(index)"
          @toggle-completed="toggleCompleted(index)" />
      </ul>
      <AddTodo @addTodo="addTodo" />
    </div>
  </template>
  
  <script>
  import TodoItem from './TodoItem.vue'
  import AddTodo from './AddTodo.vue'
  
  export default {
    components: { TodoItem, AddTodo },
    data() {
      return {
        todos: []
      } 
    },
    methods: {
        addTodo(newTodo) {
            this.todos.push({ text: newTodo, completed: false })
            this.saveTodos()
        },
        removeTodo(index) {
            this.todos.splice(index, 1)
            this.saveTodos()
        },
        toggleCompleted(index) {
            this.todos[index].completed = !this.todos[index].completed
            this.saveTodos()
        },
        saveTodos() {
            localStorage.setItem('todos', JSON.stringify(this.todos))
        }
    }
  }
  </script>
  
  <style scoped>
  h1 {
    font-family: Arial, sans-serif;
    color: #333;
  }
  
  ul {
    list-style-type: none;
    padding: 0;
  }
  
  li {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    background: #f9f9f9;
    margin: 5px 0;
    border: 1px solid #ddd;
  }
  
  button {
    background: #ff4444;
    color: white;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
  }
  
  button:hover {
    background: #ff0000;
  }
  
  input {
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    width: calc(100% - 22px);
  }
  
  button {
    background: #00aaff;
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
  }
  
  button:hover {
    background: #007acc;
  }
  </style>